import React from 'react';
import { Popover as HeadlessPopover } from '@headlessui/react';
import classNames from 'classnames';

export const Popover: React.FC<React.HTMLAttributes<HTMLDivElement>> = ({
  className,
  children,
  ...props
}) => (
  <HeadlessPopover className={classNames('relative', className)} {...props}>
    {children}
  </HeadlessPopover>
);
